@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#HeaderFood h1{
    font-size: 5vw;
}
#HeaderFood{
    margin-top: 20px;
    margin-left: 20px;
    display: inline-grid;
}
#name{
    margin-left: 30vw;
}
#HeaderFood img{
    width: 12vw;
    margin-left: 8vw;
}
.form1 {
  display: inline-block;
  position: relative;
  z-index: 1;
  background: #ffffff;
  padding: 45px;
  width: 100%;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form1 input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form1 button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #000000;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-weight: 900;
  font-size: 14px;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form1 button:hover,
.form1 button:active,
.form1 button:focus {
  background: rgb(27, 47, 97);
}
.form1 .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form1 .message a {
  color: #000000;
  text-decoration: none;
}
.FoodPost {
  display: inline-block;
  text-align: center;
  max-width: 300px;
  background-color: rgb(172, 172, 172);
  margin: 20px;
  border-radius: 20px;
  color: white;
  background-color: black;
  transition: 0.3s;
  box-shadow: 7px 7px 10px 0 rgba(0, 0, 0, 0.356);
}
.FoodPost:hover {
  transform: scale(1.05);
  transition: 0.3s;
}
.FoodPost img {
  width: 300px;
  height: 300px;
  border-radius: 20px;
  object-fit: cover;
}

.FoodPost .text {
  margin-top: -50px;
  position: relative;
  background: linear-gradient(0deg, rgba(0,0,0,0.9) 75%, rgba(0, 0, 0, 0) 100%);
  border-radius: 0px 0px 20px 20px;
}

.postIcons {
  margin-bottom: 20px;
  font-size: 40px;
  transition: 0.5s;
}
.postIcons:hover {
  transform: scale(1.1);
  transition-duration: 0.1s;
}
.postIcons.heart {
  color: rgb(216, 30, 30);
}
.postIcons.heart:hover {
  color: rgb(255, 0, 0);
}
.postIcons.delete{
  color: rgb(146, 145, 145);
}
.postIcons.delete:hover{
  color: rgb(196, 196, 196);
}

.poster{
  transition: 0.5s;
  color: rgb(201, 201, 201);
}
.poster:hover{
  transform:scale(1.1);
  transition-duration: 0.1s;
  color: rgb(223, 223, 223);
  cursor: pointer;
}

.extraInfo{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
#navBar {
  background-color: #000;
}
#navBar a {
  color: #fff;
  padding: 10px;
  transition: 0.5s;
}
#navBar a:hover {
  transform: scale(1.05);
  color: rgb(139, 221, 132);
  transition: 0.5s;
}
#navBar img {
  width: 40px;
  background: white;
  border-radius: 50%;
  margin-right: 10px;
}
#toggleNav {
  border-color: rgb(131, 131, 131);
}

